<template>
  <div class="page-container">
    <div class="tab-wrapper">
      <vue-tabs-chrome v-model="tabCurrent" :tabs="tabs" />
    </div>
    <div class="page-section info-tab-display" v-if="tabCurrent == 'info'">
      <div class="report-sheet report-sheet-info" style="grid-row: span 2">
        <div class="report-container">
          <div class="sheet-body" style="grid-template-columns: 50% 50%">
            <div class="section-label" style="grid-column: span 2">
              <label>Pressure Vessel information</label>
            </div>
            <div class="form-item" v-for="item in generalInfo" :key="item.desc">
              <div class="form-item-label">
                <label>{{ item.desc }}</label>
              </div>
              <div class="form-item-value">
                <label>{{ item.value }}</label>
              </div>
            </div>
            <!-- <div class="form-item" style="grid-column: span 2; grid-row: span 2; height: 49px">
              <div class="form-item-label" style="height: 36px">
                <label>-</label>
              </div>
              <div class="form-item-value" style="height: 36px">
                <label>placeholder</label>
              </div>
            </div>-->
          </div>
          <tableComponent />
        </div>
      </div>

      <div class="report-sheet">
        <div class="report-container">
          <div class="sheet-body">
            <div>
              <div class="section-label">
                <label>overview picture</label>
              </div>

              <div class="form-item-picture-log">
                <div class="img-box">
                  <div class="btn-panel">
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="PREVIEW_PIC(infoVessel.overview_img_path)"
                      v-if="infoVessel.overview_img_path"
                    >
                      <i class="las la-eye"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="DELETE_PIC(infoVessel.overview_img_path, 1)"
                      v-if="infoVessel.overview_img_path"
                    >
                      <i class="las la-trash"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button>
                      <label
                        class="pic-toolbar-btn"
                        for="pic-upload-btn"
                        v-if="!infoVessel.overview_img_path"
                      >
                        <i class="las la-plus"></i>
                      </label>
                    </v-ons-toolbar-button>
                  </div>
                  <img
                    :src="baseURL + infoVessel.overview_img_path"
                    v-if="infoVessel.overview_img_path"
                  />
                  <input
                    type="file"
                    id="pic-upload-btn"
                    style="display: none"
                    ref="pic_upload_ov"
                    @change="UPLOAD_PIC(1)"
                  />

                  <div class>
                    <i class="las la-image" v-if="!infoVessel.overview_img_path"></i>
                    <label v-if="!infoVessel.overview_img_path">No Image</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="report-sheet">
        <div class="report-container">
          <div class="sheet-body">
            <div>
              <div class="section-label">
                <label>name plate picture</label>
              </div>

              <div class="form-item-picture-log">
                <div class="img-box">
                  <div class="btn-panel">
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="PREVIEW_PIC(infoVessel.name_plate_img_path)"
                      v-if="infoVessel.name_plate_img_path"
                    >
                      <i class="las la-eye"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="DELETE_PIC(infoVessel.name_plate_img_path, 2)"
                      v-if="infoVessel.name_plate_img_path"
                    >
                      <i class="las la-trash"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button v-if="!infoVessel.name_plate_img_path">
                      <label
                        class="pic-toolbar-btn"
                        for="pic-upload-btn-2"
                        v-if="!infoVessel.name_plate_img_path"
                      >
                        <i class="las la-plus"></i>
                      </label>
                    </v-ons-toolbar-button>
                  </div>
                  <img
                    :src="baseURL + infoVessel.name_plate_img_path"
                    v-if="infoVessel.name_plate_img_path"
                  />
                  <input
                    type="file"
                    id="pic-upload-btn-2"
                    style="display: none"
                    ref="pic_upload_np"
                    @change="UPLOAD_PIC(2)"
                  />
                  <i class="las la-image" v-if="!infoVessel.name_plate_img_path"></i>
                  <label v-if="!infoVessel.name_plate_img_path">No Image</label>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="tabCurrent=='doc'">
      <tableGeneralDoc />
    </div>

    <div v-if="tabCurrent=='const'">
      <tableConstDoc />
    </div>

    <div v-if="tabCurrent=='insp'">
      <tableInsp />
    </div>

    <contentLoading text="Loading, please wait..." v-if="isLoading == true" color="#fc9b21" />
    <previewImage :imageURL="previewImg" v-if="previewImg" @close-preview="PREVIEW_PIC_CLOSE()" />
  </div>
</template> 

<script>
//API
import axios from "/axios.js";
import moment from "moment";

//Components
import VueTabsChrome from "vue-tabs-chrome";
import contentLoading from "@/components/app-structures/app-content-loading.vue";
import previewImage from "@/components/image-preview.vue";
import tableComponent from "@/views/Applications/Vessel/Pages/Information/table-component.vue";
import tableGeneralDoc from "./table-generalDoc.vue";
import tableConstDoc from "./table-const.vue";
import tableInsp from "./table-insp.vue";

//DataGrid

export default {
  name: "PressureVesselInformation",
  components: {
    VueTabsChrome,
    contentLoading,
    previewImage,
    tableComponent,
    tableGeneralDoc,
    tableConstDoc,
    tableInsp
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Pressure Vessel Information",
      subpageInnerName: null
    });
    this.FETCH_INFO();
  },
  data() {
    return {
      infoVessel: {},
      file_pic_upload: "",
      tabCurrent: "info",
      tabs: [
        {
          label: "General Information",
          key: "info",
          closable: false
        },
        {
          label: "Construction Document",
          key: "const",
          closable: false
        },
        {
          label: "Inspection Document",
          key: "insp",
          closable: false
        },
        {
          label: "General Document",
          key: "doc",
          closable: false
        }
      ],
      previewImg: "",
      isLoading: false
    };
  },
  computed: {
    generalInfo() {
      var info = [
        {
          desc: "Tag No.",
          value: this.infoVessel.id
        },
        {
          desc: "Insulation",
          value: this.infoVessel.insulation
        },
        {
          desc: "Pressure Vessel No.",
          value: this.infoVessel.vessel_no
        },
        {
          desc: "Insulation Type",
          value: this.infoVessel.insulation_type
        },
        {
          desc: "Site",
          value: this.infoVessel.id_site
        },
        {
          desc: "Insulation Size (mm)",
          value: this.infoVessel.insulation_size
        },
        {
          desc: "Description",
          value: this.infoVessel.vessel_desc
        },
        {
          desc: "CAd",
          value: this.infoVessel.CAd
        },
        {
          desc: "Design Code",
          value: this.infoVessel.design_code
        },
        {
          desc: "Ld",
          value: this.infoVessel.Ld
        },
        {
          desc: "Height (mm)",
          value: this.infoVessel.height_mm
        },
        {
          desc: "Internal Coating",
          value: this.infoVessel.internal_coating
        },
        {
          desc: "P&ID No.",
          value: this.infoVessel.pid_no
        },
        {
          desc: "Internal Coating Type",
          value: this.infoVessel.internal_coating_type
        },
        {
          desc: "Drawing No.",
          value: this.infoVessel.drawing_no
        },
        {
          desc: "External Coating",
          value: this.infoVessel.external_coating
        },
        {
          desc: "Construction Material",
          value: this.infoVessel.material_of_const
        },
        {
          desc: "External Coating Type",
          value: this.infoVessel.external_coating_type
        },
        {
          desc: "Inservice Date",
          value: this.infoVessel.next_insp_date
            ? moment(this.infoVessel.inservice_date).format("LL")
            : null
        },
        {
          desc: "Cladding Material",
          value: this.infoVessel.cladding_material
        },
        {
          desc: "Internal Lining",
          value: this.infoVessel.internal_lining
        },
        {
          desc: "Cladding Material Type",
          value: this.infoVessel.cladding_material_type
        },
        {
          desc: "Code Stamp",
          value: this.infoVessel.code_stamp
        },
        {
          desc: "Equipment Type",
          value: this.infoVessel.equipment_type
        },
        {
          desc: "RT",
          value: this.infoVessel.RT
        },
        {
          desc: "Equipment Desc",
          value: this.infoVessel.equipment_desc
        },
        {
          desc: "PWHT",
          value: this.infoVessel.PWHT
        },
        {
          desc: "Tube Material",
          value: this.infoVessel.tube_material
        },
        {
          desc: "Tube Outside Diameter (mm)",
          value: this.infoVessel.tube_OD_mm
        },
        {
          desc: "Tube Service",
          value: this.infoVessel.tube_service
        },
        {
          desc: "Tube Nominal Thickness (mm)",
          value: this.infoVessel.tube_t_nom
        },
        {
          desc: "Tube Type",
          value: this.infoVessel.tube_type
        },
        {
          desc: "Tube Quantity",
          value: this.infoVessel.tube_quantity
        },
        {
          desc: "Tube Length (m)",
          value: this.infoVessel.tube_length
        }
      ];
      return info;
    },
    baseURL() {
      var mode = this.$store.state.mode;
      if (mode == "dev") return this.$store.state.modeURL.dev;
      else if (mode == "prod") return this.$store.state.modeURL.prod;
      else return console.log("develpment mode set up incorrect.");
    }
  },
  watch: {
    // watch for changes in the $route object
    //note for future me : need to do this because when use search in navbar (v-select) the router.push() only change path but not create new component because we use same page so created() will not be called
    $route: {
      handler() {
        // run this logic whenever router is change
        this.FETCH_INFO();
      }
      //immediate: true // call the handler immediately when the component is created
    }
  },
  methods: {
    FETCH_INFO() {
      console.log("==> FETCH Vessel INFO: ");
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;

      axios({
        method: "get",
        url: "PressureVesselInfo/" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.infoVessel = res.data;
            console.log(this.infoVessel);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    UPLOAD_PIC(pic_type) {
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;
      if (pic_type == 1) {
        this.file_pic_upload = this.$refs.pic_upload_ov.files[0];
      } else {
        this.file_pic_upload = this.$refs.pic_upload_np.files[0];
      }
      if (
        (this.file_pic_upload && this.file_pic_upload.type == "image/jpeg") ||
        this.file_pic_upload.type == "image/png"
      ) {
        if (this.file_pic_upload.size < 20000000) {
          axios({
            method: "post",
            url: "/PressureVesselInfo/upload-pic",
            headers: {
              "Content-Type": "multipart/form-data",
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id: id_tag,
              file: this.file_pic_upload,
              type: pic_type
            }
          })
            .then(res => {
              if (res.status == 200) {
                this.FETCH_INFO();
                console.log("==> Vessel INFO IMG UPLOAD: DONE");
                this.isLoading = false;
              }
            })
            .catch(error => {
              this.isLoading = false;
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {});
        } else {
          this.$ons.notification.alert("File size too large. (20 MB max)");
          const file = document.getElementById("pic-upload-btn");
          file.val = "";
          this.isLoading = false;
        }
      } else {
        this.$ons.notification.alert(
          "Incorrect filetype. <br/> Only JPEG/PNG file can be uploaded."
        );
        const file = document.getElementById("pic-upload-btn");
        file.val = "";
        this.isLoading = false;
      }
    },
    DELETE_PIC(file_path, pic_type) {
      this.$ons.notification.confirm("Confirm delete?").then(res => {
        if (res == 1) {
          this.isLoading = true;
          const id_tag = this.$route.params.id_tag;
          axios({
            method: "delete",
            url: "/PressureVesselInfo/delete-pic",
            headers: {
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id: id_tag,
              file_path: file_path,
              type: pic_type
            }
          })
            .then(res => {
              if (res.status == 200) {
                this.FETCH_INFO();
                console.log("==> Vessel INFO IMG DELETE: DONE");
                this.isLoading = false;
              }
            })
            .catch(error => {
              this.isLoading = false;
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {
              this.FETCH_INFO();
            });
        }
      });
    },
    PREVIEW_PIC(img) {
      if (img) {
        this.previewImg = img;
      }
    },
    PREVIEW_PIC_CLOSE() {
      this.previewImg = "";
    }
  }
};
</script>

