<template>
  <div style="padding-top:10px; padding-bottom:10px">
    <DxDataGrid
      id="data-grid-style"
      key-expr="id"
      :data-source="vesselComponent"
      :element-attr="dataGridAttributes"
      :selection="{ mode: 'single' }"
      :word-wrap-enabled="true"
      :hover-state-enabled="true"
      :allow-column-reordering="true"
      :show-borders="true"
      :show-row-lines="true"
      :row-alternation-enabled="false"
      @exporting="EXPORT_DATA"
      @row-inserted="CREATE_COMPONENT"
      @row-updated="UPDATE_COMPONENT"
      @row-removed="DELETE_COMPONENT"
    >
      <DxEditing
        :allow-updating="true"
        :allow-deleting="true"
        :allow-adding="true"
        :use-icons="true"
        mode="row"
      />
      <!-- <DxToolbar>
        <DxItem location="before" template="table-header" />
        <DxItem location="after" template="table-header-button-set" />
      </DxToolbar>-->

      <DxColumn data-field="group_no" :width="0" caption />
      <DxColumn data-field="id_group_type" caption="Component" :width="100">
        <DxLookup :data-source="component_group" value-expr="id" display-expr="code" />
      </DxColumn>

      <DxColumn
        data-field="design_temp_min"
        caption="Design Temp Min"
        format="#,##0.00"
        header-cell-template="header_design_temp_min"
      />
      <DxColumn
        data-field="design_temp_max"
        caption="design_temp_max"
        format="#,##0.00"
        header-cell-template="header_design_temp_max"
      />
      <DxColumn
        data-field="operate_temp"
        caption="Operate Temperature"
        format="#,##0.00"
        header-cell-template="header_operate_temp"
      />
      <DxColumn
        data-field="design_pressure"
        caption="Design Pressure"
        format="#,##0.00"
        header-cell-template="header_design_pressure"
      />
      <DxColumn
        data-field="operate_pressure"
        caption="Operate Pressure"
        format="#,##0.00"
        header-cell-template="header_operate_pressure"
      />
      <DxColumn data-field="corrosion_loop_no" caption="Corrosion Loop No." />
      <DxColumn data-field="PWHT" caption="PWHT" />

      <template #header_design_pressure>
        <div>
          Design Pressure
          <BR />(psi)
        </div>
      </template>
      <template #header_design_temp_min>
        <div>
          Min Design Temperature
          <BR />(c)
        </div>
      </template>
      <template #header_design_temp_max>
        <div>
          Max Design Temperature
          <BR />(c)
        </div>
      </template>
      <template #header_operate_temp>
        <div>
          Operate Temperature
          <BR />(c)
        </div>
      </template>
      <template #header_operate_pressure>
        <div>
          Operate Pressure
          <BR />(psi)
        </div>
      </template>

      <!-- <template #cell-button-set="{ data }">
        <div class="table-btn-group">
          <div class="table-btn" v-on:click="EDIT_INFO(data)">
            <i class="las la-pen blue"></i>
