          <div class="table-btn" v-on:click="DELETE_INFO(data)">
            <i class="las la-trash red"></i>
          </div>
        </div>
      </template>-->
      <template #table-header>
        <div>
          <div class="page-section-label">Pressure Vessel Component</div>
        </div>
      </template>
      <!-- <template #table-header-button-set>
        <div>
          <v-ons-toolbar-button>
            <i class="las la-plus"></i>
            <span>Add New Tank Course</span>
          </v-ons-toolbar-button>
        </div>
      </template>-->
      <!-- Configuration goes here -->
      <!-- <DxFilterRow :visible="true" /> -->
      <DxScrolling mode="standard" />
      <DxSearchPanel :visible="true" />
      <DxPaging :page-size="10" :page-index="0" />
      <DxPager
        :show-page-size-selector="true"
        :allowed-page-sizes="[5, 10, 20]"
        :show-navigation-buttons="true"
        :show-info="true"
        info-text="Page {0} of {1} ({2} items)"
      />
      <DxExport :enabled="true" />
    </DxDataGrid>
  </div>
</template> 

<script>
//API
import axios from "/axios.js";
import saveAs from "file-saver";
import { exportDataGrid } from "devextreme/excel_exporter";
//import moment from "moment";

//Components

//DataGrid
import "devextreme/dist/css/dx.light.css";
import { Workbook } from "exceljs";
import {
  DxDataGrid,
  DxSearchPanel,
  DxPaging,
  DxPager,
  DxScrolling,
  DxColumn,
  DxExport,
  //DxToolbar,
  //DxItem,
  DxEditing,
  DxLookup
} from "devextreme-vue/data-grid";

export default {
  name: "component-table",
  components: {
    DxDataGrid,
    DxSearchPanel,
    DxPaging,
    DxPager,
    DxScrolling,
    DxColumn,
    DxExport,
    //DxToolbar,
    //DxItem,
    DxEditing,
    DxLookup
  },
  created() {
    if (this.$store.state.status.server == true) {
      this.FETCH_COMPONENT();
      this.FETCH_MD_COMPONENT_GROUP();
    }
  },
  data() {
    return {
      vesselComponent: {},
      component_group: [],
      dataGridAttributes: {
        class: "data-grid-style"
      }
    };
  },
  computed: {},
  methods: {
    EXPORT_DATA(e) {
      const workbook = new Workbook();
      const worksheet = workbook.addWorksheet("Projects");
      exportDataGrid({
        worksheet: worksheet,
        component: e.component
      }).then(function() {
        workbook.xlsx.writeBuffer().then(function(buffer) {
          saveAs(
            new Blob([buffer], { type: "application/octet-stream" }),
            "Projects.xlsx"
          );
        });
      });
      e.cancel = true;
    },
    FETCH_COMPONENT() {
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;
      axios({
        method: "get",
        url:
          "PressureVesselComponentGroup/get-pv-com-by-id?id_vessel=" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.vesselComponent = res.data;
            console.log(this.vesselComponent);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    CREATE_COMPONENT(e) {
      e.data.id = 0;
      e.data.id_vessel = parseInt(this.$route.params.id_tag);
      console.warn(e.data);

      axios({
        method: "post",
        url: "/PressureVesselComponentGroup",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: e.data
      })
        .then(res => {
          if (res.status == 201) {
            console.log("create success");
            this.FETCH_COMPONENT();
          }
        })
        .catch(error => {
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {});
    },
    UPDATE_COMPONENT(e) {
      axios({
        method: "put",
        url: "PressureVesselComponentGroup/" + e.data.id,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: e.data
      })
        .then(res => {
          if (res.status == 200) {
            console.log("update success");
            this.FETCH_COMPONENT();
          }
        })
        .catch(error => {
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {});
    },
    DELETE_COMPONENT(e) {
      axios({
        method: "delete",
        url: "PressureVesselComponentGroup/" + e.data.id,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 204) {
            console.log("delete success");
            this.FETCH_COMPONENT();
          }
        })
        .catch(error => {
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {});
    },
    FETCH_MD_COMPONENT_GROUP() {
      axios({
        method: "get",
        url: "master-data/get-md-pv-com-group",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.status == 200 && res.data) {
          this.component_group = res.data;
        }
      });
    }
  }
};
</script>

